<script>
import { clearToken } from '@/assets/javascript/methods';

export default {
  methods: {
    signOut() {
      clearToken();
      const token = document.cookie.replace(
        /(?:(?:^|.*;\s*)LULU-Bake\s*=\s*([^;]*).*$)|^.*$/,
        '$1',
      );
      this.$http.defaults.headers.common.Authorization = `Bearer ${token}`;
      this.$router.push('/');
      this.$emitter.emit('messageModal', { status: true, message: 'Logout Successfully' });
    },
  },
  created() {
    this.signOut();
  },
};
</script>
